.profile__name {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 16px 0 16px;
    max-width: 375px;
}

@media screen and (max-width: 475px) {
    .profile__name {
        font-size: 22px;
        line-height: 27px;
        max-width: 200px;
        margin: 0;
    }
  }