.popup__input {
  border: none;
  width: 358px;
  padding: 0 0 8px;
  border-bottom: 1px solid #c5c5c5;
  color: #000;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 38px;
}

@media screen and (max-width: 600px) {
  .popup__input {
    width: 238px;
  }
}

.popup__input:invalid {
  margin-bottom: 38px;
}
