.header{
    max-width: 880px;
    margin: 45px auto 0;
    padding-bottom: 40px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 925px) {
    .header {
      max-width: 75%;
    }
  }

  @media screen and (max-width: 600px) {
    .header {
      max-width: 100vw;
      margin: 28px auto 0;
      padding-bottom: 32px;
    }
  }